import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
//carousel
import VueAgile from 'vue-agile'
import 'vue-agile/dist/VueAgile.css'
Vue.use(VueAgile)

//chart
import VueApexCharts from 'vue-apexcharts'
Vue.use(VueApexCharts)

Vue.component('apexchart', VueApexCharts)

Vue.config.productionTip = false

/**
 * framework ui
 */
import Vant , { Locale } from 'vant';
import enUS from 'vant/lib/locale/lang/en-US';
import 'vant/lib/index.css';
Locale.use('en-US', enUS);
Vue.use(Vant);
import VueLazyload from 'vue-lazyload';
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'img/loader.png',
  loading: 'img/loader.svg',
  attempt: 1
});

/**
 * Other
 */
import Splitting from './assets/core/splitting.js';
import WOW from './assets/core/wow.js';
import './assets/core/splitting-wow.scss';
window.Splitting = Splitting;
window.WOW = WOW;

import VanillaTilt from 'vanilla-tilt';
window.VanillaTilt = VanillaTilt;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
