<template>
<div class="card3d-wrap"
  @mousemove="handleMouseMove"
  @mouseenter="handleMouseEnter"
  @mouseleave="handleMouseLeave"
  ref="card3d">
  <div class="card3d"
    :style="card3dStyle">
    <div class="card3d-bg" :style="card3dBgTransform" v-lazy:background-image="dataImage"></div>
    <div class="card3d-info">
      <slot name="header"></slot>
      <slot name="content"></slot>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'Card3d',
  props: ['dataImage'],
  data: () => ({
    width: 0,
    height: 0,
    mouseX: 0,
    mouseY: 0,
    mouseLeaveDelay: null
  }),
  computed: {
    mousePX() {
      return this.mouseX / this.width;
    },
    mousePY() {
      return this.mouseY / this.height;
    },
    card3dStyle() {
      const rX = this.mousePX * 30;
      const rY = this.mousePY * -30;
      return {
        transform: `rotateY(${rX}deg) rotateX(${rY}deg)`
      };
    },
    card3dBgTransform() {
      const tX = this.mousePX * -40;
      const tY = this.mousePY * -40;
      return {
        transform: `translateX(${tX}px) translateY(${tY}px)`
      }
    },
  },
  methods: {
    handleMouseMove(e) {
      let vm = this;
      vm.mouseX = e.pageX - vm.$el.offsetLeft - vm.width/2;
      vm.mouseY = e.pageY - vm.$el.offsetTop - vm.height/2;
    },
    handleMouseEnter() {
      let vm = this;
      clearTimeout(vm.mouseLeaveDelay);
    },
    handleMouseLeave() {
      let vm = this;
      vm.mouseLeaveDelay = setTimeout(()=>{
        vm.mouseX = 0;
        vm.mouseY = 0;
      }, 200);
    }
  },
  mounted(){
    let vm = this;
    vm.width = vm.$el.offsetWidth;
    vm.height = vm.$el.offsetHeight;
  }
};
</script>
<style lang="scss">
$hoverEasing: cubic-bezier(0.23, 1, 0.32, 1);
$returnEasing: cubic-bezier(0.445, 0.05, 0.55, 0.95);

