<template>
  <canvas></canvas>
</template>

<script>
export default {
  name: "Matrix3d",
  props: ["height"],
  mounted(){
    let vm = this;
    // Initialising the canvas
    let cs = vm.$el,
        ctx = cs.getContext('2d');

    // Setting the width and height of the canvas
    cs.width = window.innerWidth;
    cs.height = vm.height || 300;

    // Setting up the letters
    let letters = 'ABCDEFGHIJKLMNOPQRSTUVXYZABCDEFGHIJKLMNOPQRSTUVXYZABCDEFGHIJKLMNOPQRSTUVXYZABCDEFGHIJKLMNOPQRSTUVXYZABCDEFGHIJKLMNOPQRSTUVXYZABCDEFGHIJKLMNOPQRSTUVXYZ';
    letters = letters.split('');

    // Setting up the columns
    let fontSize = 10,
        columns = cs.width / fontSize;

    // Setting up the drops
    let drops = [];
    for (let i = 0; i < columns; i++) {
      drops[i] = 1;
    }

    // Setting up the draw function
    function draw() {
      ctx.fillStyle = 'rgba(0, 0, 0, .1)';
      ctx.fillRect(0, 0, cs.width, cs.height);
      for (let i = 0; i < drops.length; i++) {
        let text = letters[Math.floor(Math.random() * letters.length)];
        ctx.fillStyle = '#0f0';
        ctx.fillText(text, i * fontSize, drops[i] * fontSize);
        drops[i]++;
        if (drops[i] * fontSize > cs.height && Math.random() > .95) {
          drops[i] = 0;
        }
      }
    }

    // Loop the animation
    setInterval(draw, 33);

    window.addEventListener('resize', function(){
      cs.width = window.innerWidth;
      columns = cs.width / fontSize;
      // Setting up the drops
      drops = [];
      for (let i = 0; i < columns; i++) {
        drops[i] = 1;
      }
    });
  }
};
</script>
<style lang="scss">
canvas {display: block;}
</style>